<script lang="ts">
  import { onMount } from "svelte";
  import periodToDays from "../../../lib/period"

  function defaultLayout() {
    let periodAgo = new Date();
    let days = periodToDays(period);
    if (days != null) {
      periodAgo.setDate(periodAgo.getDate() - days);
    } else {
      periodAgo = null;
    }
    let tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate());
    return {
      title: false,
      autosize: true,
      margin: { r: 35, l: 70, t: 10, b: 20, pad: 0 },
      hovermode: "closest",
      plot_bgcolor: "transparent",
      paper_bgcolor: "transparent",
      height: 170,
      yaxis: {
        title: { text: "Response time (ms)" },
        gridcolor: "gray",
        showgrid: false,
        fixedrange: true,
      },
      xaxis: {
        title: { text: "Date" },
        showgrid: false,
        fixedrange: true,
        range: [periodAgo, tomorrow],
        visible: false,
      },
      dragmode: false,
    };
  }

  function bars() {
    let responseTimes = {};
    let days = periodToDays(period);
    if (days) {
      for (let i = 0; i < days; i++) {
        let date = new Date();
        date.setHours(0, 0, 0, 0);
        date.setDate(date.getDate() - i);
        // @ts-ignore
        responseTimes[date] = { total: 0, count: 0 };
      }
    }

    for (let i = 0; i < data.length; i++) {
      let date = new Date(data[i].created_at);
      date.setHours(0, 0, 0, 0);
      // @ts-ignore
      if (!(date in responseTimes)) {
        // @ts-ignore
        responseTimes[date] = { total: 0, count: 0 };
      }
      // @ts-ignore
      responseTimes[date].total += data[i].response_time;
      // @ts-ignore
      responseTimes[date].count++;
    }

    let requestFreqArr = [];
    for (let date in responseTimes) {
      let point = [new Date(date), 0];
      if (responseTimes[date].count > 0) {
        point[1] = responseTimes[date].total / responseTimes[date].count;
      }
      requestFreqArr.push(point);
    }
    requestFreqArr.sort((a, b) => {
      return a[0] - b[0];
    });

    let dates = [];
    let requests = [];
    for (let i = 0; i < requestFreqArr.length; i++) {
      dates.push(requestFreqArr[i][0]);
      requests.push(requestFreqArr[i][1]);
    }

    return [
      {
        x: dates,
        y: requests,
        type: "lines",
        marker: { color: "#707070" },
        // fill: "tonexty",
        hovertemplate: `<b>%{y:.1f}ms avg</b><br>%{x|%d %b %Y}</b><extra></extra>`,
        showlegend: false,
      },
      {
        x: dates,
        y: Array(requests.length).fill(Math.max(Math.min(...requests) - 5, 0)),
        type: "lines",
        marker: { color: "transparent" },
        fill: "tonexty",
        fillcolor: "#4A4A4A",
        hovertemplate: `<b>%{y:.1f}ms avg</b><br>%{x|%d %b %Y}</b><extra></extra>`,
        showlegend: false,
      },
    ];
  }

  function buildPlotData() {
    return {
      data: bars(),
      layout: defaultLayout(),
      config: {
        responsive: true,
        showSendToCloud: false,
        displayModeBar: false,
      },
    };
  }

  function genPlot() {
    let plotData = buildPlotData();
    //@ts-ignore
    new Plotly.newPlot(
      plotDiv,
      plotData.data,
      plotData.layout,
      plotData.config
    );
  }

  let plotDiv: HTMLDivElement;
  let mounted = false;
  onMount(() => {
    mounted = true;
  });

  $: data && mounted && genPlot();

  export let data: RequestsData, period: string;
</script>

<div id="plotly">
  <div id="plotDiv" bind:this={plotDiv}>
    <!-- Plotly chart will be drawn inside this DIV -->
  </div>
</div>
